.aboutMeSection {
    /* border: solid 1px black; */
    width: 100vw;
    height: calc(100vh - 6rem);
    background-color: #fafafc;
}

.aboutMeSection .aboutMeContainer {
    /* border: solid 1px black; */
    height: calc(100vh - 6rem);
    margin: 0 12vw;
    display: flex;
    flex-direction: column;
    color: #222;
}

.aboutMeSection .aboutMeContainer .containerHeader {
    /* border: solid 1px #e5e5e5; */
    border-radius: 12px 12px 0 0;
    width: 100%;
    height: 25%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
}

.aboutMeSection .aboutMeContainer .containerHeader h2 {
    /* text-shadow: 0px 0px 10px blue; */
    /* color: white; */
}

.aboutMeSection .aboutMeContainer .containerBox {
    /* border: solid 2px red; */
    height: 75%;
    display: flex;
}

.aboutMeSection .aboutMeContainer .containerBox .containerLeft {
    /* border: solid 1px #e5e5e5; */
    border-top: 0;
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.aboutMeSection .aboutMeContainer .containerBox .containerLeft .containerLeftBox {
    /* border: solid 1px red; */
    width: 460px;
    /* display: flex;
    flex-direction: column; */
    /* margin: auto; */
    /* margin-top: 2rem; */
}

.aboutMeSection .aboutMeContainer .containerBox .containerLeft .containerLeftName {
    /* border: solid 1px black; */
    font-size: 1.5rem;
    margin-bottom: 2rem;
}

.aboutMeSection .aboutMeContainer .containerBox .containerLeft .containerLeftName .headerColorBox {
    width: 84px;
    height: 20px;
    /* border: solid 1px black; */
    border-radius: 16px 0 16px 0;
    background-color: #fff3b2;
    display: flex;
    align-items: center;
}

.aboutMeSection .aboutMeContainer .containerBox .containerLeft .containerLeftContent {
    display: flex;
    flex-direction: column;
    margin-bottom: 2rem;
    color: #555;
    position: relative;
}
/* .aboutMeSection .aboutMeContainer .containerBox .containerLeft .containerLeftContent .leftContentGroup {
    display: flex;
} */

.aboutMeSection .aboutMeContainer .containerBox .containerLeft .containerLeftContent span {
    font-family: "paybooc-Medium";
    font-size: 18px;
    z-index: 1;
}
.aboutMeSection .aboutMeContainer .containerBox .containerLeft .containerLeftContent .boldContent {
    /* border: solid 1px black; */
    font-family: "paybooc-Bold";
    color: #222;
}

.aboutMeSection .aboutMeContainer .containerBox .containerRight {
    /* border: solid 1px blue; */
    border-left: solid 1px #e5e5e5;
    width: 50%;
    height: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.moreMe:visited,
.moreMe:link {
    /* border: solid 1px black; */
    color: #226dd0;
    text-decoration-line: none;
}
.moreMe:hover {
    color: #6326cb;
    transition: all 0.8s;
}
